<script>
import RowOperableTable from "@/views/Tables/RowOperableTable";
import SellCustomerInfoForm from "@/components/MyComponents/SellCustomerInfoForm";
import Vue from 'vue'
import VueClipboard from 'vue-clipboard2'

Vue.use(VueClipboard)

export default {
    name: "Sell",
    components: {
        SellCustomerInfoForm,
        RowOperableTable
    },
    data: () => (
        {
            productCount: 0
        }
    ),
    methods: {
        handleClick() {
            let type
            let title
            if (this.productCount === 0) {
                type = 'warning'
                title = '尚未添加任何数据'
            } else {
                type = 'success'
                title = '提交成功'
            }
            this.$notify({
                type,
                title
            })
        },
        fromOperableTable(directive, arg) {
            switch (directive) {
                case 'productCount': {
                    this.productCount = arg
                    break
                }
                default: {
                    break
                }
            }
        }
    }
}
</script>

