<template>
    <div>
        <base-header type="gradient-success" class="pb-6 pb-8 pt-5 pt-md-8"/>
        <div class="container-fluid mt--7">
            <PurchaseSupplierInfoForm/>
            <RowOperableTable title="入库" mode="purchase" @fromOperableTable="fromOperableTable"/>
            <div class="row" style="margin-top: 20px;">
                <div class="col-12">
                    <base-button type="default" block size="lg" @click="handleClick" :disabled="productCount === 0">
                        <span>提交</span>
                        <badge type="primary">{{ productCount }}</badge>
                    </base-button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import RowOperableTable from "@/views/Tables/RowOperableTable";
import PurchaseSupplierInfoForm from "@/components/MyComponents/PurchaseSupplierInfoForm";

export default {
    components: {
        PurchaseSupplierInfoForm,
        RowOperableTable
    },
    data: () => (
        {
            productCount: 0
        }
    ),
    methods: {
        handleClick() {
            let type
            let title
            if (this.productCount === 0) {
                type = 'warning'
                title = '尚未添加任何数据'
            } else {
                type = 'success'
                title = '提交成功'
            }
            this.$notify({
                type,
                title
            })
        },
        fromOperableTable(directive, arg) {
            switch (directive) {
                case 'productCount': {
                    this.productCount = arg
                    break
                }
                default: {
                    break
                }
            }
        }
    }
}
</script>
